import { environment } from './../../../../environments/environment.prod';
import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { catchError, map } from 'rxjs/operators'
import { throwError, Observable } from 'rxjs';
import { userModel, SessionUserModel } from '../models';
import { HttpClientService } from './shared/http.service';



@Injectable()
export class AuthService {


  get CurrentUser() {
    let user: SessionUserModel = null;
    let sessionUser = sessionStorage.getItem('current-user');
    if (sessionUser)
      user = SessionUserModel.CreateUser(JSON.parse(sessionUser));

    return user;
  }


  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json'
    })
  };

  constructor(private http: HttpClientService) { }
  baseUrl = environment.backEndApi;
  serviceUrl = this.baseUrl + 'doctor';
  //https://newwebservice.elbalto.com/App/SignUpData
  login(phone: string, password: string): Observable<boolean> {

     let body = { phone, password };
    // let jsonBody = JSON.stringify(body);

    return this.http.post<userModel>(this.serviceUrl + '/login', body, this.httpOptions).pipe(map(res => {
      let user = SessionUserModel.CreateUser(res.Data);
      sessionStorage.setItem('current-user', JSON.stringify(user));
      return true;
    }));
  }

}
