import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import * as $ from 'jquery';
import { OwlOptions } from 'ngx-owl-carousel-o';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  encapsulation: ViewEncapsulation.None
})
export class HomeComponent implements OnInit {
  customOptions: OwlOptions = {
    loop: true,
    mouseDrag: true,
    touchDrag: true,
    pullDrag: true,
    rtl: true,
    nav: true,
    // tslint:disable-next-line:max-line-length
    navText: ['<i class="fas fa-arrow-left bg-white shadow rounded-circle p-2"></i>', '<i class="fas fa-arrow-right  bg-white shadow rounded-circle p-2"></i>'],
    responsive: {
      0: {
        items: 1
      },
      70: {
        items: 2
      },
      120: {
        items: 3
      },
      200: {
        items: 4
      },
      300: {
        items: 5
      },
      450: {
        items: 5
      }
    }
  };
  constructor() { }
  ngOnInit() {
    // tslint:disable-next-line:only-arrow-functions
    $(document).ready(function () {

      // tslint:disable-next-line:only-arrow-functions
      $('#add-Reservation').click(function () {
        $('#box-Add').css({ display: 'flex' });
      });
      $('.Reservation-home').not($('.Reservation-home:first')).hide();

      $('.Reservation-home-schedule .day').click(function () {
        const myId = $(this).attr('id');
        $('.Reservation-home').hide();
        $('.' + myId + '-Reservation').slideDown(500);
      });

      $('.Reservation-home-schedule .day').click(function () {
        $(this).addClass('activeDay');
        $('.Reservation-home-schedule .day').not(this).removeClass('activeDay');

      });

    });
  }


}
