import { ServiceBag } from './utils/services/shared/servcie-bag';
import { NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule } from '@angular/common';

import { HomeRoutingModule } from './home.routing';
import { HomeComponent } from './pages/home/home.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { TopnavComponent } from './components/topnav/topnav.component';
import { SidemessageComponent } from './components/sidemessage/sidemessage.component';
import { CarouselModule } from 'ngx-owl-carousel-o';
import { AuthService } from './utils';
import { HomeContentComponent } from './components/home-content/home-content.component';
import { HttpClientService } from './utils/services/shared/http.service';
import { HttpClientModule } from '@angular/common/http';
import { RouterModule } from '@angular/router';


@NgModule({
  declarations: [HomeComponent, NavbarComponent, TopnavComponent, SidemessageComponent, HomeContentComponent],
  exports: [
    NavbarComponent, TopnavComponent, SidemessageComponent, HttpClientModule
  ],
  imports: [
    CommonModule,
    HomeRoutingModule,
    HttpClientModule,
    CarouselModule,
    RouterModule
  ]
})
export class HomeModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: HomeModule,
      providers: [AuthService, HttpClientService, ServiceBag]
    };
  }
}
