import { ApiBaseModel } from './baseModel';
import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { Observable, throwError, iif, timer } from 'rxjs';
import { map, catchError, retry, retryWhen, concatMap, tap } from 'rxjs/operators';
import { isArray } from 'util';
@Injectable()
export class HttpClientService {

  constructor(private http: HttpClient) {

  }

  get<T>(url: string): Observable<ApiBaseModel<T>>;
  get<T>(url: string, params: any): Observable<ApiBaseModel<T>>;
  get<T>(url: string, params: any, skipAuth: boolean): Observable<ApiBaseModel<T>>;
  get<T>(url: string, params: any, skipAuth: boolean, silent: boolean): Observable<ApiBaseModel<T>>;
  get<T>(url: string, params?: any, skipAuth?: boolean, silent?: boolean): Observable<ApiBaseModel<T>> {
    let headers = new HttpHeaders();
    return this.http.get<ApiBaseModel<T>>(url, {
      params: params,
      observe: 'response',
      withCredentials: false,
      headers: headers
    }).pipe(
      map((res: HttpResponse<ApiBaseModel<T>>) => {
        return this.buildResponse<T>(res);
      }),
      catchError((err: HttpErrorResponse) => this.handleError(err, this, silent))
    );
  }

  post<T>(url: string, data: any, params?: any): Observable<ApiBaseModel<T>> {
    let headers = new HttpHeaders();
    return this.http.post<ApiBaseModel<T>>(url, data, {
      params: params,
      observe: 'response',
      withCredentials: false,
      headers: headers
    }).pipe(
      map((res: HttpResponse<ApiBaseModel<T>>) => {
        return this.buildResponse<T>(res);
      }),
      catchError((err: HttpErrorResponse) => this.handleError(err, this))
    );
  }

  private handleError(err: HttpErrorResponse, thisArg: HttpClientService, silent = false) {
    var errors = [];
    return throwError(err);
  }

  private buildResponse<T>(res: HttpResponse<ApiBaseModel<T>>) {
    if (!res.body.Status)
      throw new HttpErrorResponse({ error: res.body.Status.Id, headers: res.headers, status: res.status, statusText: res.statusText, url: res.url });
    return res.body;
  }

}
