
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  // { path: '', redirectTo: 'home', pathMatch: 'full' },
  {
    path: 'signup',
    loadChildren: () => import('./sign-up/sign-up.module').then(signup => signup.SignUpModule)
  },
  {
    path: '',
    loadChildren: () => import('./home/home.module').then(home => home.HomeModule)
  },
  {
    path: 'login',
    loadChildren: () => import('./login/login.module').then(login => login.LoginModule)
  }
  


];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
