import { userModel } from '.';

export class SessionUserModel {
  first_name_en: string = '';
  last_name_en: string = '';
  first_name_ar: string = '';
  last_name_ar: string = '';
  phone: string = '';
  code: string = '';
  image: string = '';
  email: string = '';
  type: string = '';
  id_provider: string = '';
  oneSiganlToken: string = '';
  video_token: string = '';
  birth_date: string = '';
  langauge: string = '';
  time_zone: number = undefined;
  token: string = '';
  identifier: string = '';


  static CreateUser(user?: userModel) {
    if (!user)
      throw 'user cannot be null';
    if (user)
      return SessionUserModel.createUserFromInstance(user);
  }

  static createUserFromInstance(user: userModel) {
    let userModel = new SessionUserModel();
        return Object.assign(userModel, user);
  }
}