import { Observable } from 'rxjs/internal/Observable';
import { HttpClientService } from './http.service';
import { map } from 'rxjs/operators';
import { AppBaseModel, ApiBaseModel } from './baseModel';
import { Injectable } from '@angular/core';

@Injectable()
export class ServiceBag {

  constructor(private httpClient: HttpClientService) {
  }

  get<T>(url: any, params: any): Observable<AppBaseModel<T>> {
    return this.httpClient.get(url, params).pipe(map(res => {
      return this.createBaseModel<T>(res);
    }));
  }

  post<T>(url: any, params: any, data?: any): Observable<AppBaseModel<T>> {
    return this.httpClient.post(url, data, params).pipe(map(res => {
      return this.createBaseModel<T>(res);
    }));
  }

  private createBaseModel<TOut>(res: ApiBaseModel<any>) {
    let response = new AppBaseModel<TOut>();
    response.status = res.Status.Id;
    response.result = res.Data;
    return response;
  }

}