import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './pages/home/home.component';
import { HomeContentComponent } from './components/home-content/home-content.component';


const routes: Routes = [
  {
    path: '',
    component: HomeComponent,
    children: [
      {
        path: '',
        component: HomeContentComponent
      },
      {
        path: 'agenada',
        loadChildren: () => import('../features/agenada/agenada.module').then(agenda => agenda.AgenadaModule)
      },
      {
        path: 'editprofile',
        loadChildren: () => import('../features/edit-profile/edit-profile.module').then(editprofile => editprofile.EditProfileModule)
      },
      {
        path: 'medical',
        loadChildren: () => import('../features/medical/medical.module').then(medical => medical.MedicalModule)
      },
      {
        path: 'profile',
        loadChildren: () => import('../features/profile/profile.module').then(profile => profile.ProfileModule)
      },
      {
        path: 'reservation',
        loadChildren: () => import('../features/reservation/reservation.module').then(reservation => reservation.ReservationModule)
      },
      {
        path: 'reservationpast',
        loadChildren: () => import('../features/reservation-past/reservation-past.module').then(reservationpast => reservationpast.ReservationPastModule)
      },
      {
        path: 'videocall',
        loadChildren: () => import('../features/video-call/video-call.module').then(videocall => videocall.VideoCallModule)
      },
      {
        path: 'wallet',
        loadChildren: () => import('../features/wallet/wallet.module').then(wallet => wallet.WalletModule)
      },
      {
        path: 'chat',
        loadChildren: () => import('../features/chat/chat.module').then(chat => chat.ChatModule)
      },
      {
        path: 'inbox',
        loadChildren: () => import('../features/inbox/inbox.module').then(inbox => inbox.InboxModule)
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HomeRoutingModule { }
